@keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
.container {
  max-width: 100%;
  width: 600px;
  margin: 100px auto;
}

.el {
  width: 10px;
  height: 10px;
  background-color: pink;
  transition-duration: 300ms;
  opacity: 1;
}
.el--animation-fade-out {
  animation: fadeOut 300ms infinite;
}
.el--transition-fade-out {
  opacity: 0;
}